import { Stack, IconButton, ButtonGroup, ToggleButtonGroup, ToggleButton } from '@mui/material';
import Button from '@mui/material/Button'
import SendIcon from '@mui/icons-material/Send';
import { useState } from 'react';
import FormatBoldIcon from '@mui/icons-material/FormatBold';
import FormatItalicIcon from '@mui/icons-material/FormatItalic';
import FormatUnderlinedIcon from '@mui/icons-material/FormatUnderlined';
import FormatColorFillIcon from '@mui/icons-material/FormatColorFill';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

export const MultiButton = ()=>{
    const [formats,setFormats] = useState(["bold"]);
    const text = formats.join(" ")

    return (
        <Stack spacing={4}>
            {/*  */}
            <Stack spacing={2} direction='row'>
                <Button variant="text" href='http://data.slhs.tp.edu.tw/hong'>Button 1</Button>
                <Button variant="contained">Button 2</Button>
                <Button variant="outlined">Button 3</Button>
            </Stack>

            {/*  */}
            <Stack spacing={2} direction='row'>
                <Button variant="contained" color="error">error</Button>
                <Button variant="contained" color="info">info</Button>
                <Button variant="contained" color="primary">primary</Button>
                <Button variant="contained" color="secondary">secondary</Button>
                <Button variant="contained" color="success">success</Button>
                <Button variant="contained" color="warning">warning</Button>
            </Stack>

            {/*  */}
            <Stack display="block" spacing={2} direction='row'>
                <Button variant="contained" size="small">small</Button>
                <Button variant="contained" size="medium">medium</Button>
                <Button variant="contained" size="large">large</Button>
            </Stack>

            {/* Icon, IconButtonIcon */}
            <Stack spacing={2} direction='row'>
                <Button variant="contained" startIcon={<SendIcon/>} onClick={()=>alert('Hello World!')}>Send</Button>
                <Button variant="contained" endIcon={<SendIcon/>} disableElevation disableRipple>Send</Button>
                <IconButton aria-label="Send" color='primary' size="large">
                    <SendIcon />
                </IconButton>
            </Stack>

            {/* ButtonGroupButton */}
            <Stack direction='row'>
                <ButtonGroup variant="contained" size="medium" color="secondary" orientation="vertical">
                  <Button onClick={()=>alert("")}>Left</Button>
                  <Button>Middle</Button>
                  <Button>Right</Button>
                </ButtonGroup>
            </Stack>

            {/*  */}
            <Stack direction='row' spacing={2}>
                <ToggleButtonGroup size="large" orientation='vertical' value={formats} onChange={(e,newValue)=>setFormats(newValue)}>
                    <ToggleButton value="bold"><FormatBoldIcon /></ToggleButton>
                    <ToggleButton value="italic"><FormatItalicIcon /></ToggleButton>
                    <ToggleButton value="underlined"><FormatUnderlinedIcon/></ToggleButton>
                    <ToggleButton value="color">
                        <FormatColorFillIcon />
                        <ArrowDropDownIcon />
                    </ToggleButton>
                </ToggleButtonGroup>

                <p className={text}>
                    
                </p>
            </Stack>
        </Stack>
    );
}

/*
    Stackcontainer
    direction='row'
    direction='column'
    spacing={}

    displayflexsizeblockinlineinline-block
    disableElevation:   
    disableRipple: 

    IconButton: IconButton

    ToggleButtonGroup: useState""
    ToggleButtonGrouponChangefunctionevent,
    exclusive: 


    array to string join:  const  s = [1,2,3,4].join(" ")   =>   1 2 3 4
*/