import Typography from '@mui/material/Typography'

export const MultiTypography = ()=>{
    return (
        <>
            <Typography variant="h1" color="initial">h1</Typography>
            <Typography variant="h2" color="initial">h2</Typography>
            <Typography variant="h3" color="initial" component="h1" gutterBottom>h3</Typography>
            <Typography variant="h4" color="initial">h4</Typography>
            <Typography variant="h5" color="red">h5</Typography>
            <Typography variant="h6" color="initial">h6</Typography>

            <Typography variant="subtitle1" color="initial">subtitle1</Typography>
            <Typography variant="subtitle2" color="initial">subtitle2</Typography>

            <Typography variant="body1" color="initial">body1</Typography>
            <Typography variant="body2" color="initial">body2</Typography>
        </>
    );
}

/*
    variant: muih1<h1></h1>
    component: varianttagvariant="h1"<h1>component="h3"<h3>mui h1
    gutterBottom: 
*/